const imageFetch = {

    options: {
        method: 'GET',
        mode: 'no-cors',
    },

    imageURL(imgId) {
        return "images/" + imgId
    },

    fullSizeImageURL(imgId) {
        return "images/fullSize/" + imgId
    },

    async download(url) {
        let rawRes = await fetch(new Request(url), this.options),
            buffer = await rawRes.arrayBuffer();

        return this.parse(buffer);
    },

    parse(arrayBuffer) {
        let binary = '',
            bytes = [].slice.call(new Uint8Array(arrayBuffer));

        bytes.forEach((b) => binary += String.fromCharCode(b));
        return 'data:image/jpeg;base64,' + window.btoa(binary);
    },

};

export default imageFetch;