import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import config from "../common/config"

import myCacheStorage from "../common/cache"

const cache = myCacheStorage('api');

const APIService = {
    init() {
        Vue.use(VueAxios, axios);
        Vue.axios.defaults.baseURL = config.API_URL();
        /*Vue.axios.defaults.headers.post["Pragma"] = "";
        Vue.axios.defaults.headers.post["Cache-Control"] = "";*/
    },

    query(resource, params) {
        return Vue.axios.get(resource, params).catch(error => {
            throw new Error(`[RWV] ApiService ${error}`);
        });
    },

    get(resource, slug = "") {
        return Vue.axios.get(`${resource}/${slug}`).catch(error => {
            throw new Error(`[RWV] ApiService ${error}`);
        });
    },

    postWithCaching(resource, params, lifeTime) {
        if (cache !== null) {
            let cacheId = resource + "/";
            Object.entries(params).forEach(entry => {
                let key = entry[0];
                let value = entry[1];
                cacheId += `${key}:${value};`;
            });

            return cache.has(cacheId).then(has => {
                if (has) {
                    return cache.get(cacheId).then(res => {
                        return res.json().then(parsedRes => {
                            return {
                                status: 200,
                                isFromCache: true,
                                data: parsedRes,
                            }
                        })
                    });
                } else {
                    return Vue.axios.post(`${resource}`, params).then((res) => {
                        if (res.status === 200) {
                            cache.add(cacheId, res.request.response, lifeTime);
                        }
                        return res;
                    });
                }
            });
        } else {
            return Vue.axios.post(`${resource}`, params);
        }
    },

    post(resource, params) {
        return Vue.axios.post(`${resource}`, params);
    },

    update(resource, slug, params) {
        return Vue.axios.put(`${resource}/${slug}`, params);
    },

    put(resource, params) {
        return Vue.axios.put(`${resource}`, params);
    },

    delete(resource) {
        return Vue.axios.delete(resource).catch(error => {
            throw new Error(`[RWV] ApiService ${error}`);
        });
    }
};

export default APIService;