<template>
    <div v-if="isVisible">
        <img class="center" v-show="!isLoading"
             v-bind:style="{opacity: opacity}" ref="img"
             @click="clickHandler"/>

        <LoadingSpinner class="center" v-show="isLoading"
                        v-bind:style="{opacity: opacity}" />
    </div>
</template>

<script>
    import {EventBus, Events} from "../../common/eventBus";
    import {HOME_URL} from "../../common/config";
    import myCacheStorage from "../../common/cache"
    import imageFetch from "../../common/imageFetch"
    import LoadingSpinner from "./LoadingSpinner";

    const cache = myCacheStorage('fullSizeImages');

    export default {
        name: "ImagePreviewContainer",

        components: {LoadingSpinner},

        data: () => {
            return {
                isVisible: false,
                isLoading: false,
                opacity: 0,
                img: "",
            }
        },

        methods: {
            clickHandler() {
                EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 15});
                this.hide();
                EventBus.$off(Events.onBackgroundClick);
            },

            show() {
                this.isVisible = true;
                setTimeout(() => {
                    this.opacity = 1;
                }, 50);
            },

            hide() {
                this.opacity = 0;
                setTimeout(() => {
                    this.isVisible = false;
                }, 350)
            },

            getUrl(img) {
                return HOME_URL() + "/" + img;
            },
        },

        created() {
            EventBus.$on(Events.showImagePreview, (img) => {
                console.log("show img:", {img});
                this.isLoading = true;
                let url = this.getUrl(img);

                this.show();
                let download = () => {
                    imageFetch.download(url)
                        .then((res) => {
                            this.isLoading = false;
                            this.$refs.img.src = res;

                            if (cache !== null)
                                cache.add(url, res, 3600);
                        })
                        .catch((error) => {
                            console.error(error);
                        });
                };

                if (cache !== null) {
                    cache.has(this.getUrl(img))
                        .then((has) => {
                            if (has) {
                                cache.get(url)
                                    .then(res => res.text())
                                    .then((img) => {
                                        this.isLoading = false;
                                        this.$refs.img.src = img;
                                    })
                            } else {
                                download();
                            }
                        });
                } else {
                    download();
                }

                EventBus.$emit(Events.showUnfocusedBackground, {zIndex: 16});
                EventBus.$on(Events.onBackgroundClick, () => {
                    EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 16});
                    this.hide();
                    EventBus.$off(Events.onBackgroundClick);
                })
            });

            window.addEventListener('scroll', () => {
                if (this.isVisible) {
                    EventBus.$emit(Events.hideUnfocusedBackground, {zIndex: 16});
                    this.hide();
                    EventBus.$off(Events.onBackgroundClick);
                }
            });
        },

        destroyed() {
            EventBus.$off(Events.showImagePreview);
        },
    }
</script>

<style lang="sass" scoped>

    img
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.52), 0 10px 10px rgba(0, 0, 0, 0.5)

    .center
        margin: auto
        position: fixed
        top: 0
        left: 0
        bottom: 0
        right: 0
        z-index: 17
        transition: 0.35s
</style>